@import "../../styles/variables.scss";

.dashboard{
    width: 100%;
    height: 100vh;

    .dashboard-main{
        width: 100%;
        height: 100vh;

        .dashboard-boxes{
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            border: solid 1px $soft;
            padding: 1%;
            grid-auto-rows: minmax(150px, auto);
            grid-auto-flow: dense;

            @media (max-width: 1024px) {
                    grid-template-columns: repeat(2, 1fr);
                }
            
                @media (max-width: 768px) {
                    grid-template-columns: repeat(1, 1fr);
                }
            
                @media (max-width: 480px) {
                    grid-template-columns: repeat(1, 1fr);
                }

            .box{
                // border: $solid;
                
                box-shadow: $soft 1px 3px 7px;
                margin: 10px;
                padding: 5px;
                border-radius: 7px;
                gap : $gap
            }
            .box3{
                grid-column: span 1;
                grid-row: span 3;
            }

            .box2 {
                grid-column: span 1;
                grid-row: span 3;
            }
        }
    }
}